import { useState } from "react";
import "./styles.css";

export default function App() {
  const [cars, updateCars] = useState(["ford", "honda"]);
  const newCars = ["kia", "tata"];
  const newCarList = (e) => {
    updateCars([cars, ...newCars], []);
  };
  const car = cars.map((car) => <ul>{car}</ul>);
  const addnewCars = (e) => {};
  return (
    <div className="App">
      <li>{car}</li>
      <input type="text" onChange={addnewCars}></input>
      <div>
        <input type="button" onClick={newCarList} value="Add" />,
      </div>
    </div>
  );
}
